import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import {isAuthenticated} from './auth.js';

/* minhas páginas */
import Site from './site/site.jsx';
import Login from './app/Login/login.jsx';
import Registro from './app/Registro/registro.jsx';
import PainelMenu from './app/Painel/painel-menu.jsx';
import PainelAtendimento from './app/Painel/painel-atendimento.jsx';
import PainelCliente from './app/Painel/painel-cliente.jsx';
import PainelResultado from './app/Painel/painel-resultado.jsx';
import PainelPendencia from './app/Painel/painel-pendencia.jsx';
import Agendamento from './app/Form/agendamento.jsx';
import Calendario from './app/Components/Calendario/calendario.jsx';

const PrivateRoute = ({ component: Component, ...rest }) => (
    <Route
      {...rest}
      render={props =>
        isAuthenticated() ? (
          <Component {...props} />
        ) : (
          <Redirect to={{ pathname: "/app", state: { from: props.location } }} />
        )
      }
    />
);

function App(){
    return <BrowserRouter>
        <Switch>
            <Route exact path='/' component={Site} />
            <Route exact path='/app' component={Login} />
            <Route exact path='/app/registro' component={Registro} />
            <PrivateRoute exact path='/app/menu_principal' component={PainelMenu} />
            <PrivateRoute exact path='/app/atendimento' component={PainelAtendimento} />
            <PrivateRoute exact path='/app/cliente' component={PainelCliente} />
            <PrivateRoute exact path='/app/resultado' component={PainelResultado} />
            <PrivateRoute exact path='/app/pendencia' component={PainelPendencia} />
            <PrivateRoute exact path='/app/agendamento' component={Agendamento} />
            <PrivateRoute exact path='/app/calendario' component={Calendario} />
        </Switch>
    </BrowserRouter>;
}

export default App;

