import React from 'react';
import {Link} from 'react-router-dom';
import './registro.css';

function Registro(){
    return <div className="d-flex align-items-center text-center form-container">
        
        <form className="box" action="\app\menu_principal">       
            <p className="text-light fs-5 text-page">Crie sua conta no e-Consult</p>    
            <div className="form-floating">
                <input type="text" className="form-control campo-input" id="floatingInput" placeholder="Crie seu login" />
                <label for="floatingInput">Crie seu login</label>
            </div>
            <div className="form-floating">
                <input type="password" className="form-control campo-input" id="floatingPassword" placeholder="Informe uma senha" />
                <label for="floatingPassword">Informe uma senha</label>
            </div>
            <div className="form-floating">
                <input type="password" className="form-control campo-input" id="floatingSecondPassword" placeholder="Repita sua senha" />
                <label for="floatingSecondPassword">Repita sua senha</label>
            </div>           
            <button className="w-100 btn btn-lg btn-success" type="submit">Criar conta</button>
            <div className="login-links">
                <Link to="/app" className="text-light mx-3">Eu já tenho uma conta</Link> 
            </div>
            <p className="text-light">&copy; Desenvolvido por NBS Code</p>
        </form>      
    </div>;
}

export default Registro;