import React from 'react';
import {Link} from 'react-router-dom';
import './form.css';

function Agendamento(){
    return <div className="container-painel">
        <div className="menu-top-painel">
            <div className="titulo-painel"><h4>Agendamento</h4></div>
            <div className="img-usuario"><img src="../images/btn-img-usuario.png" alt="Ícone" /></div>
        </div>

        <div className="justify-content-center nav-top-painel">

        </div>  

        <div className="boddy-painel">     

            <form>
                <div className="form-floating mb-2">
                    <input type="text" className="form-control" id="cliente" />
                    <label for="cliente">Cliente</label>
                </div>
                <div className="form-floating mb-2">
                    <input type="date" className="form-control" id="data" />
                    <label for="data">A partir de</label>
                </div>
                <div className="form-floating mb-2">
                    <select className="form-select" id="dia-semana">
                        <option value="1">Domingo</option>
                        <option selected value="2">Segunda-feira</option>
                        <option value="3">Terça-feira</option>
                        <option value="4">Quarta-feira</option>
                        <option value="5">Quinta-feira</option>
                        <option value="6">Sexta-feira</option>
                        <option value="7">Sábado</option>
                    </select>
                    <label for="dia-semana">Agendar em toda(o)</label>
                </div>
                <div className="form-floating mb-2">
                    <input type="time" className="form-control" id="hora" />
                    <label for="hora">Hora</label>
                </div>  
                <div className="form-floating mb-2">
                    <input type="number" className="form-control" id="valor" />
                    <label for="hora">Valor por atendimento (R$)</label>
                </div> 
                <div className="form-floating mb-2">
                    <select className="form-select" id="modalidade">
                        <option selected value="P">Presencial</option>
                        <option value="R">Remoto</option>
                    </select>
                    <label for="modalidade">Modalidade</label>
                </div>
                <div className="form-floating mb-2">
                    <select className="form-select" id="periodicidade">
                        <option selected value="S">Toda semana</option>
                        <option value="Q">A cada quinze dias</option>
                        <option value="M">A cada mês</option>
                    </select>
                    <label for="periodicidade">Periodicidade</label>
                </div>                
                <div className="d-grid gap-2">
                    <button type="submit" className="btn btn-success">Verificar disponibilidade</button>
                </div>
            </form>

        </div>

        <div className="menu-botton-painel">
            <div className="btn-img-botton"></div>
            <div className="btn-img"><img src="../images/png-black/fi-rr-settings.png" width="50" height="" alt="Ícone"  /></div>
            <div className="btn-img-botton"><Link to="/#"><img src="../images/png-black/fi-rr-calendar.png" width="50" height="" alt="Ícone" /></Link></div>
            <div className="btn-img-botton"></div>
        </div>
    </div>;
}

export default Agendamento;