import React from 'react';
import {Link} from 'react-router-dom';
import './pendencia.css';

function Pendencia(props){
    return <div className="container container-pendencia">
        <Link to="#" className="text-decoration-none text-reset">
        <div className="d-flex justify-content-start">
            <div className="text-start col-pendencia-btn">
                <img src={props.icone} height="35px" width="" className="img-pendencia" alt="Ícone" />                 
                <div className="col-pendencia-detalhe">
                    <p><strong>{props.tipo}</strong></p>
                    <p>{props.nomepessoa}</p>
                    <p>ATENDIMENTO: {props.atendimento}</p>
                </div>
            </div>
        </div>
        </Link>
    </div>;
}

export default Pendencia;