import React from 'react';
import {Link} from 'react-router-dom';
import './painel.css';

function Painel(){
    return <div className="container-painel-back">
        <div className="menu-top-painel">
            <div className="titulo-painel"><h4>Menu principal</h4></div>
            <div className="img-usuario"><img src="../images/btn-img-usuario.png" /></div>
        </div>

        <div className="justify-content-center nav-top">

        </div>  

        <div className="boddy-painel">     

            <Link to="#" className="text-decoration-none text-reset">
                <img className="img-especialista" src="../images/btn-img-usuario.png" width="40" height="" />            
                <p className="text-center text-uppercase fs-5 text-especialista">Lorita Ângela Serpa</p>
                <p className="text-center fs-6 text-especialidade">Psicóloga</p>
            </Link>

            <div className="list-group">
                <Link to="#" className="list-group-item list-group-item-action">Agendamento</Link>
                <Link to="#" className="list-group-item list-group-item-action">Clientes</Link>
                <Link to="#" className="list-group-item list-group-item-action">Adicionar cliente</Link>
                <Link to="#" className="list-group-item list-group-item-action">Faturas do cliente</Link>
                <Link to="#" className="list-group-item list-group-item-action">Documentos do cliente</Link>
                <Link to="#" className="list-group-item list-group-item-action">Receitas e despesas</Link>
                <Link to="#" className="list-group-item list-group-item-action">Faturamento</Link>
                <Link to="#" className="list-group-item list-group-item-action">Remarcações e desmarcações</Link> 
                <Link to="#" className="list-group-item list-group-item-action">Configurações</Link>                                  
            </div>

        </div>

        <div className="menu-botton-painel">
            <div className="btn-img-botton"></div>
            <div className="btn-img"><img src="../images/png-black/fi-rr-interlining.png" width="50" height="" /></div>
            <div className="btn-img-botton"><Link to="/app/atendimento"><img src="../images/png-black/fi-rr-calendar.png" width="50" height="" /></Link></div>
            <div className="btn-img-botton"><Link to="/app/cliente"><img src="../images/png-black/fi-rr-users.png" width="50" height="" /></Link></div>
            <div className="btn-img-botton"><Link to="/app/resultado"><img src="../images/png-black/fi-rr-stats.png" width="50" height="" /></Link></div>
            <div className="btn-img-botton"><Link to="/app/pendencia"><img src="../images/png-black/fi-rr-list-check.png" width="50" height="" /></Link></div>
            <div className="btn-img-botton"></div>
        </div>
    </div>;
}

export default Painel;