import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './painel.css';
import ListaCliente from '../../api/api-cliente-lista-cliente.jsx'
import Cliente from '../Components/Painel/cliente'

function ObterProps(props){
    const Login = props.Login;
    const Senha = props.Senha;
    return true;
}

class Clientes extends Component{

    state= {
        apiListaCliente: [],
    }

    async componentDidMount(){
        const response = await ListaCliente.get('');
        this.setState({ apiListaCliente: response.data})
    }

    render(){

        const {apiListaCliente} = this.state;

        return (
            <div className="container-painel-back">
                
                <div className="menu-top-painel">
                    <div className="titulo-painel"><h4>Clientes</h4></div>
                    <div className="img-usuario"><img src="../images/btn-img-usuario.png" /></div>
                </div>

                <div className="justify-content-center nav-top">
                    <div className="d-flex justify-content-center nav-titulo">
                        <h6>incluir cliente</h6>
                    </div>
                    <div className="d-flex justify-content-center">
                        <Link to="#"><img src="../images/png-black/fi-rr-add.png" width="35" height="" /></Link>
                    </div>
                </div>   

                <div className="boddy-painel">
                    
                    {apiListaCliente.map(vcliente => (
                        < Cliente key={vcliente.IDCLIENTE} 
                                       nomecliente={vcliente.NOMEPESSOA} 
                                       email={vcliente.EMAIL}
                                       telefone={vcliente.TELEFONE}
                                       celular={vcliente.CELULAR}
                                       grupo={vcliente.DESCRICAOGRUPO} 
                                       valor={vcliente.VALORCOBRANCA}
                                       qtatendimentos={vcliente.AGENDAMENTOS}
                                       />
                    ))}                     

                    <div className="margem-botton-100"></div>         
                </div>

                <div className="menu-botton-painel">
                    <div className="btn-img-botton"></div>
                    <div className="btn-img-botton"><Link to="/app/menu_principal"><img src="../images/png-black/fi-rr-interlining.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"><Link to="/app/atendimento"><img src="../images/png-black/fi-rr-calendar.png" width="50" height="" /></Link></div>
                    <div className="btn-img"><img src="../images/png-black/fi-rr-users.png" width="50" height="" /></div>
                    <div className="btn-img-botton"><Link to="/app/resultado"><img src="../images/png-black/fi-rr-stats.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"><Link to="/app/pendencia"><img src="../images/png-black/fi-rr-list-check.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"></div>
                </div>
            </div>
        );    
    }
}

export default Clientes;

