import React from 'react';
import StringMask from 'string-mask';
import {Link} from 'react-router-dom';
import './cliente.css';

var formatMoney = new StringMask('#.##0,00', {reverse: true});

function Cliente(props){
    return <div className="container container-cliente">
        <div className="d-flex row-cliente">
            <div className="col-11 col-cliente-detalhe">
                <p className="fw-bold tit-cliente-detalhe">{props.nomecliente}</p>   
                <p className="fw-bold des-cliente-detalhe">{props.email}</p>   
                <p className="fw-bold des-cliente-detalhe">{props.telefone}  {props.celular}</p> 
                <p className="des-cliente-detalhe text-xl-start text-uppercase"><strong>{props.grupo}</strong>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Valor por atendimento: R${formatMoney.apply(props.valor*100)}</p>  
                <p className="des-cliente-detalhe text-uppercase">Agendamentos: ({0 + props.qtatendimentos})</p>     
            </div>
            <div className="col-1 col-cliente-btn">
                <Link to="#"><img src="../images/png-color/fi-rr-pencil.png" height="35px" width="" alt="Ícone" /></Link>
            </div>
        </div>
    </div>;
}

export default Cliente;