import React, {Component, useState} from 'react';
import {Link} from 'react-router-dom';
import UsuarioValidaLogin from '../../api/api-usuario-valida-login.jsx'
import './login.css';

class ValidaLogin extends Component{
    state= {
        apiUsuarioValidaLogin: ['Login=lorys&Senha=123456'],
    }

    async componentDidMount(){
        const response = await UsuarioValidaLogin.get('');
        this.setState({ apiUsuarioValidaLogin: response.data})
    }

    render(){
        const {apiUsuarioValidaLogin} = this.state;
        return
        {console.log(apiUsuarioValidaLogin.map(vLogin => (alert(vLogin.NomeUsuario))))}
        <h1>sucesso</h1>
    }
}

export default ValidaLogin;