import React from 'react';
import StringMask from 'string-mask';
import './atendimento.css';

var formatvalorpago = new StringMask('PAGO R$ #.##0,00', {reverse: true});
var formatvalorcombinado = new StringMask('TOTAL R$ #.##0,00', {reverse: true});

function Atendimento(props){
    return  <div>
        {props.tipo === 'I' ?
        
            <div className="container container-intervalo">
                <div className="d-flex row-intervalo">
                    <div className="col-2 col-intervalo-hora">
                        <p className="fw-bold">{(props.horainicio).substring(0,5) }</p>
                    </div> 

                    <div className="col-9 col-intervalo-detalhe">
                        <p className="tit-intervalo-detalhe fw-bold">{props.nomecliente}</p>    
                    </div>
                </div>
            </div>

        :
            props.tipo === 'D' ?
            
                <div className="container container-disponivel">
                    <div className="d-flex row-disponivel">
                        <div className="col-2 col-disponivel-hora">
                            <p className="fw-bold">{(props.horainicio).substring(0,5) }</p>
                        </div> 

                        <div className="col-9 col-disponivel-detalhe">
                            <p className="tit-disponivel-detalhe fw-bold">{props.nomecliente}</p>    
                        </div>

                        <div className="col-1 col-atendimento-btn">
                            <a href="/#"><img src="../images/png-color/fi-rr-add.png" height="35px" width="" alt="Ícone" /></a>
                        </div>
                    </div>
                </div>
            :
                <div className="container container-atendimento">
                    <div className="d-flex row-atendimento">
                        <div className="col-2 col-atendimento-hora">
                            <p className="fw-bold">{(props.horainicio).substring(0,5) }</p>
                        </div> 
                        <div className="col-9 col-atendimento-detalhe">
                            <p className="tit-atendimento-detalhe fw-bold">{props.nomecliente}</p>
                            <p className="des-atendimento-detalhe">{props.modalidade}</p>   
                            <p className="des-atendimento-detalhe">TIPO PAGAMENTO: {props.tipocobranca}</p>
                            <p className="des-atendimento-detalhe">{formatvalorcombinado.apply(props.valorcombinado * 100)}&nbsp;&nbsp; 
                                                                   {formatvalorpago.apply(props.valorpago * 100)}</p> 
                            <p className="des-atendimento-detalhe">{props.status}</p>      
                        </div>
                        <div className="col-1 col-atendimento-btn">
                            <a href="/#"><img src="../images/png-color/fi-rr-pencil.png" height="35px" width="" alt="Ícone" /></a>
                        </div>
                    </div>
                </div>
        }
    </div>
}

export default Atendimento;