import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './painel.css';
import ObtemAgendaDoDia from '../../api/api-agenda-obtem-agenda-do-dia.jsx'
import Atendimento from '../Components/Painel/atendimento.jsx';

class Painel extends Component{

    state= {
        apiObtemAgendaDoDia: [],
    }

    async componentDidMount(){
        const response = await ObtemAgendaDoDia.get('');
        this.setState({ apiObtemAgendaDoDia: response.data})
    }

    render(){        
        const {apiObtemAgendaDoDia} = this.state;        
        return (        
            <div className="container-painel-back">
                <div className="menu-top-painel">
                    <div className="titulo-painel"><h4>Atendimentos</h4></div>
                    <div className="img-usuario"><img src="../images/btn-img-usuario.png" alt="Imagem de usuário" /></div>
                </div>

                <div className="justify-content-center nav-top">
                    <div className="d-flex justify-content-center"><img src="../images/png-black/fi-rr-angle-small-left.png" alt="Ícone" width="35" height="" /></div>
                    <div className="d-flex justify-content-center nav-titulo">
                        <h6>sábado - 24/07/2021</h6>
                    </div>
                    <div className="d-flex justify-content-center"><img src="../images/png-black/fi-rr-angle-small-right.png" width="35" height="" alt="Ícone"/></div>
                    <div className="d-flex justify-content-center"><img src="../images/png-black/fi-rr-refresh.png" width="35" height="" alt="Ícone"/></div>
                </div>     

                <div className="boddy-painel">
                {apiObtemAgendaDoDia.map(vagenda => (
                        <Atendimento key={vagenda.HORAINICIO} 
                                        tipo={vagenda.TIPO}
                                        horainicio={vagenda.HORAINICIO} 
                                        horafim={vagenda.HORAFIM}
                                        nomecliente={vagenda.NOME} 
                                        modalidade={vagenda.MODALIDADE} 
                                        tipocobranca={vagenda.TIPOCOBRANCA} 
                                        valorpago={vagenda.VALORPAGO} 
                                        valorcombinado={vagenda.VALORCOMBINADO} 
                                        status={vagenda.STATUS} 
                                        />
                ))}                          
                    <div className="margem-botton-100"></div>  
                </div>
            
                <div className="menu-botton-painel">
                    <div className="btn-img-botton"></div>
                    <div className="btn-img-botton"><Link to="/app/menu_principal"><img src="../images/png-black/fi-rr-interlining.png" width="50" height="" alt="Ícone" /></Link></div>
                    <div className="btn-img"><img src="../images/png-black/fi-rr-calendar.png" width="50" height="" alt="Ícone"/></div>
                    <div className="btn-img-botton"><Link to="/app/cliente"><img src="../images/png-black/fi-rr-users.png" width="50" height="" alt="Ícone"/></Link></div>
                    <div className="btn-img-botton"><Link to="/app/resultado"><img src="../images/png-black/fi-rr-stats.png" width="50" height="" alt="Ícone"/></Link></div>
                    <div className="btn-img-botton"><Link to="/app/pendencia"><img src="../images/png-black/fi-rr-list-check.png" width="50" height=""alt="Ícone"/></Link></div>
                    <div className="btn-img-botton"></div>
                </div>
            </div>
        );
    }
}

export default Painel;