import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Pendencia from '../Components/Painel/pendencia.jsx';
import ListaAlerta from '../../api/api-agendamento-lista-alerta.jsx'


class Alertas extends Component{

    state= {
        apiListaAlerta: [],
    }

    async componentDidMount(){
        const response = await ListaAlerta.get('');
        this.setState({ apiListaAlerta: response.data})
    }

    render(){
        const {apiListaAlerta} = this.state;
        return (
            <div className="container-painel-back">

                <div className="menu-top-painel">
                    <div className="titulo-painel"><h4>Pendências</h4></div>
                    <div className="img-usuario"><img src="../images/btn-img-usuario.png" /></div>
                </div>

                <div className="justify-content-center nav-top">
                    
                </div>                   

                <div className="boddy-painel">
                    
                    {apiListaAlerta.map(propsPendencia => (
                        < Pendencia key={propsPendencia.IDAGENDAMENTO} 
                                       nomepessoa={propsPendencia.NOMEPESSOA}
                                       tipo={propsPendencia.TIPOALERTA} 
                                       atendimento={propsPendencia.DATAINICIO}
                                       icone={propsPendencia.TIPOICONE}
                                       />
                    ))}      

                    <div className="margem-botton-100"></div>         
                </div>

                <div className="menu-botton-painel">
                    <div className="btn-img-botton"></div>
                    <div className="btn-img-botton"><Link to="/app/menu_principal"><img src="../images/png-black/fi-rr-interlining.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"><Link to="/app/atendimento"><img src="../images/png-black/fi-rr-calendar.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"><Link to="/app/cliente"><img src="../images/png-black/fi-rr-users.png" width="50" height="" /></Link></div>
                    <div className="btn-img-botton"><Link to="/app/resultado"><img src="../images/png-black/fi-rr-stats.png" width="50" height="" /></Link></div>
                    <div className="btn-img"><img src="../images/png-black/fi-rr-list-check.png" width="50" height="" /></div>
                    <div className="btn-img-botton"></div>
                </div>                

            </div>
        );    
    }
}

export default Alertas;