import React, {useState} from 'react';
import {Link, Redirect} from 'react-router-dom';
import ValidaLogin from './valida-login.jsx';
import Authenticated, { isAuthenticated } from '../../auth.js'
import './login.css';


/*function initialState(){
    return{user: '', password:''};
}*/

function Login(){   
    
    const [Login, setLogin] = useState();
    const [Senha, setSenha] = useState();

    function EfetuarLogin(event){        
        return window.location.href="/app/atendimento"
    } 

    function alterarLogin(event){
        setLogin(event.target.value);   
    }

    function alterarSenha(event){
        setSenha(event.target.value);   
    }

    return <div className="d-flex align-items-center text-center form-container">
        <form className="box">
            <p className="text-light ola">Olá!</p>
            <p className="text-light fs-6 text-page">Seja bem vindo ao e-Consult</p>

            <div className="form-floating">
            <input 
                id="Login" 
                type="text" 
                name="Login" 
                className="form-control campo-input" 
                placeholder="Login" 
                onChange={alterarLogin}
            />
            <label htmlFor="Login">Login</label>
            </div>
            <div className="form-floating">
            <input 
                id="password" 
                type="password" 
                name="password" 
                className="form-control campo-input" 
                placeholder="Senha"
                onChange={alterarSenha}
            />
            <label htmlFor="password">Senha</label>
            </div>

            <button className="w-100 btn btn-lg btn-success" type="button" onClick={EfetuarLogin}>Entrar</button>

            <div className="login-links">
                <Link to="#" className="text-light mx-3">Esqueci a senha</Link>
                <Link to="/app/registro" className="text-light mx-3">Criar uma conta</Link>    
            </div>
            <p className="text-light">&copy; Desenvolvido por NBS Code</p>
        </form>      
    </div>;
}

export default Login;