import React from 'react';

function Banner(){
    return <section id="banner">
        <div className="container">
            <div className="row">
                <div className="col-lg-6">
                    <h1>O mais completo e eficiente sistema de agendamentos para psicólogos.</h1>
                    <h4>Gerencie melhor seu tempo e se aproxime mais do seu paciente!</h4>
                    <a href="/app/registro" target="_blank" className="btn btn-dark btn-lg btn-banner">Crie já uma conta!</a>
                    <a href="/app" target="_blank" className="btn btn-outline btn-light btn-banner">Já tenho uma conta.</a>
                </div>
                <div className="col-lg-6">
                    <img src="images/image-banner-site.jpg" />
                </div>
            </div>
        </div>        
    </section>;
}

export default Banner;